/**
 * Cookie
 */

// cookievalue
export const getCookieValue = key => {
  let cookies = document.cookie.split(";") // cookie(id)
  let value = null
  for (let i in cookies) {
    let kv = cookies[i].split("=") // cookiecookie
    let temp_key = kv[0].replace(" ", "") // cookie  
    if (key == temp_key) {
      value = kv[1]
      break
    }
  }
  return value
}



