export default {
  state: {
    iframeUrl: "", // 
    iframeUrls: [] // URL
  },
  getters: {},
  mutations: {
    setIFrameUrl(state, iframeUrl) {
      // iframeUrl
      state.iframeUrl = iframeUrl;
    },
    addIFrameUrl(state, iframeUrl) {
      // iframeUrls
      state.iframeUrls.push(iframeUrl);
    }
  },
  actions: {}
};



