import axios from "../axios"

// 
export const login = data => {
  return axios.request({
    url: "/api/v1/login",
    method: "post",
    data
  })
}

// 
export const logout = () => {
  return axios.request({
    url: "/api/v1/logout",
    method: "post"
  })
}



