export default {
  state: {
    mainTabs: [], // 
    currTabName: "", // 
    allTabsClosed: true // tab
  },
  mutations: {
    updateMainTabs(state, tabs) {
      state.mainTabs = tabs;
    },
    updateCurrTabName(state, name) {
      state.currTabName = name;
    },
    onAllTabsClosed(state, status) {
      state.allTabsClosed = status;
    }
  }
};



