import Vue from "vue";
import Router from "vue-router";

import store from "@/store";
import Index from "@/views/Index";
import { getIFramePath, getIFrameUrl } from "@/common/utils/iframe";
import api from "@/common/network/api";
import Login from "@/views/Login";
import NotFound from "@/views/Error/404";
// import planCaseDetail from "@/common/components/TesterView/sprintTestPlan/planCaseDetail";

import { Message } from "element-ui";

import Cookies from "js-cookie";

Vue.use(Router);

// 
const staticRoute = [
  {
    path: "/",
    name: "Index",
    component: Index
  },

  {
    path: "/login",
    name: "",
    component: Login
  },
//   {
//     path: "/view/plan/testCase/detail",
//     name: "",
//     component: planCaseDetail
//   },
  {
    path: "/error/404",
    name: "notFound",
    component: NotFound
  }
];

const router = new Router({
  mode: "history", //   http://localhost:8080/##
  routes: staticRoute
});

router.beforeEach((to, from, next) => {
  //   let userId = sessionStorage.getItem("userId") //  // tabtabuserId
  let token = Cookies.get("token"); // token

  if (to.path === "/login") {
    // token
    if (token) {
      next({ path: "/" });
    } else {
      // 
      next();
    }
  } else {
    if (to.meta.requireAuth) {
      // ,
      if (!token) {
        // token
        next({
          path: "/login",
          query: { redirect: to.fullPath } // path
        });
      } else {
        // 
        addDynamicMenuAndRoutes(to, from, next);
      }
    } else {
      // 
      addDynamicMenuAndRoutes(to, from, next);
    }
  }
});

/**
 * 
 */
function addDynamicMenuAndRoutes(to, from, next) {
  // IFrame viewurl
  // IFrameurlIFrameurl
  // getDynamicRoutesiframeurl
  // getDynamicRoutesgetMenu

  let path = getIFramePath(to.path);
  if (path) {
    // iframe
    let url = getIFrameUrl(menuList[i].url);
    store.commit("setIFrameUrl", url);
  }

  if (store.state.app.menuRouteLoaded) {
    console.log(".");
    next();
    return;
  }

  //vuex
  let navMenuData = store.state.menu.navMenuData;
  if (navMenuData != undefined) {
    // 
    let dynamicRoutes = getDynamicRoutes(navMenuData);

    // (path "/")
    // router.options.routes[0]
   
    router.options.routes[0].children = [].concat(dynamicRoutes);
    if (router.options.routes[router.options.routes.length - 1].path != "*") {
      router.options.routes = router.options.routes.concat([
        {
          path: "*",
          name: "notFound",
          component: NotFound
        }
      ]);
      }

    // 
    router.addRoutes(router.options.routes);

    // list
    store.commit("setNavMenu", navMenuData);

    // 
    store.commit("setMenuRouteLoadStatus", true);

    next();
    if (to.matched.length == 0) {
      router.push(to.path);
    }
  } else {
    // vuex
    api.sysUser
      .getUserMenus()
      .then(res => {
        // 
          let dynamicRoutes = getDynamicRoutes(res.data);

        // 
          router.options.routes[0].children = [].concat(dynamicRoutes);

        if (
          router.options.routes[router.options.routes.length - 1].path != "*"
        ) {
          router.options.routes = router.options.routes.concat([
            {
              path: "*",
              name: "notFound",
              component: NotFound
            }
          ]);
          }

        router.addRoutes(router.options.routes);

        // list
        store.commit("setNavMenu", res.data);

        // 
        store.commit("setMenuRouteLoadStatus", true);

        next();

        if (to.matched.length == 0) {
          router.push(to.path);
        }
      })
      .catch(res => {
        Message.error(res.msg||res.message);
      });
  }
}

/**
 * ()
 * @param {*} menuList 
 * @param {*} routes ()
 */
function getDynamicRoutes(menuList = [], parentRoute = []) {
  for (var i = 0; i < menuList.length; i++) {
    var route = {}; // 
    if (menuList[i].url && /\S/.test(menuList[i].url)) {
      // url
      route = {
        path: menuList[i].url,
        component: null,
        name: menuList[i].name,
        children: [],
        meta: {
          icon: menuList[i].icon,
          index: menuList[i].id,
          requireAuth: menuList[i].requireAuth, // truefalse 
          isTopNav: menuList[i].isTopNav, // 
          topNavIndex: menuList[i].topNavId, // 
          topNavUrl: menuList[i].topNavUrl, // 
          keepAlive: false,  // false- true-
          ifCached:false,   //  
        }
      };

      let path = getIFramePath(menuList[i].url);
      if (path) {
        // , iframe
        route["path"] = path;
        route["component"] = resolve =>
          require([`@/views/IFrame/IFrame`], resolve);
        let url = getIFrameUrl(menuList[i].url);
        // URL
        store.commit("setIFrameUrl", url);
      } else {
        try {
          // URLvuevueurl
          // url="sys/user""@/views/sys/user.vue",
          let array = [];
          if (menuList[i].url.startsWith("/")) {
            // url  "/" "/" @/views//sys/user
            array = menuList[i].url.substring(1).split("/");
          } else {
            array = menuList[i].url.split("/");
          }

          let url = ""; // url

          // 
          for (let i = 0; i < array.length; i++) {
            url +=
              array[i].substring(0, 1).toUpperCase() +
              array[i].substring(1) +
              "/";
          }
          url = url.substring(0, url.length - 1); //  '/'
          route["component"] = resolve => require([`@/views/${url}`], resolve);
        } catch (e) {
          console.log("URLvue" + e);
        }
      }
      if (menuList[i].children && menuList[i].children.length >= 1) {
        getDynamicRoutes(menuList[i].children, route["children"]);
      }
    } else {
      if (menuList[i].children && menuList[i].children.length >= 1) {
        getDynamicRoutes(menuList[i].children, parentRoute);
      }
    }
    if (JSON.stringify(route) != "{}") {
      parentRoute.push(route);
    }
    }
  return parentRoute;
}


export default router;



