export const elDialogMixin = {
  data() {
    return {
      dialogWidth: "35%", // 
      showClose: true, // 
      closeOnClickModal: false, // 
      closeOnPressEscape: true, // Esc
      dialogFormLabeWidth: "120px", // 
      requestData: null // 
    };
  },
  methods: {
    onCloseDialog() {
      this.closeDialog();
    },
    closeDialog() {
      this.$emit("update:dialogVisible", false);
    },

    add(addAPI) {
      if (!this.requestData) {
        this.requestData = this.dialogForm;
      }
      addAPI(this.requestData)
        .then(res => {
          if (res.success) {
            this.$message.success(res.msg);
            this.closeDialog();

            this.tableData.splice(0, 0, res.data);
            this.$parent.total += 1;

            if (!this.$parent.isPagination) {
              // 
              return;
            }
            if (this.tableData.length > this.$parent.pageSize) {
              // 
              this.tableData.splice(-1, 1);
            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .catch(res => {
          this.$message.error(res.msg || res.message);
        });
    },
    update(updateAPI) {
      if (!this.requestData) {
        this.requestData = this.dialogForm;
      }
      updateAPI(this.requestData)
        .then(res => {
          if (res.success) {
            this.$message.success(res.msg);
            this.closeDialog();

            for (let key in res.data) {
              if (key in this.row) {
                this.row[key] = res.data[key];
              }
            }
          } else {
            this.$message.error(res.msg);
          }
        })
        .catch(res => {
          this.$message.error(" " + res.msg || res.message);
        });
    }
  }
};



