// import customUtils from "@/common/utils"
import axios from "axios";
import Cookies from "js-cookie";
import router from "@/router/";
import store from "@/store";
import Vue from "vue";

import { Message } from "element-ui";

let isRefreshing = false; // isRefreshingtokentoken

function request(options) {
    return new Promise((resolve, reject) => {
    const instance = axios.create({
      baseURL: Vue.prototype.$appConfig.API_BASE_URL,
      headers: Vue.prototype.$appConfig.headers,
      timeout: Vue.prototype.$appConfig.API_REQUEST_TIMEOUT,
      withCredentials: Vue.prototype.$appConfig.withCredentials,
      responseType: Vue.prototype.$appConfig.responseType
    });

    // request 
    instance.interceptors.request.use(
      config => {
        let token = Cookies.get("token");
        // 2. token
        if (token) {
          config.headers.Authorization = "Token " + token;
        }

        return config;
      },

      error => {
        // 
        // 1. 
        if (
          error.code === "ECONNABORTED" &&
          error.message.indexOf("timeout") !== -1
        ) {
          console.log("timeout");
        }
        // 2. 
        const errorInfo = error.response;
        if (errorInfo) {
          error = errorInfo.data;
          const errorStatus = errorInfo.status; // 404 403 500 ...
          router.push({
            path: `/error/${errorStatus}`
          });
        }
        return Promise.reject(error); // (catch)
      }
    );

    // response 
    instance.interceptors.response.use(
      response => {
        let data;
        // IE9response.dataundefinedresponse.request.responseText(Stringify)
        if (response.data == undefined) {
          data = JSON.parse(response.request.responseText);
        } else {
          data = response.data;
        }
        if (Object.prototype.toString.call(data) == "[object Blob]") {
          // 
          return response;
        } else {
          return data;
        }
      },
      err => {
        if (err && err.response) {
          switch (err.response.status) {
            case 400:
              err.msg = `: ${err.response.data.msg}`;
              break;
            case 401:
              err.msg = "";
              break;
            case 403: {
              err.msg = ``;
              break;
            }
            case 404:
              err.msg = `: ${err.response.config.url}`;
              break;
            case 408:
              err.msg = "";
              break;
            case 500:
              err.msg = ` ${
                err.response.data.msg ? err.response.data.msg : ""
              }`;
              break;
            case 501:
              err.msg = "";
              break;
            case 502:
              err.msg = "";
              break;
            case 503:
              err.msg = "";
              break;
            case 504:
              err.msg = "";
              break;
            case 505:
              err.msg = "HTTP";
              break;
            default:
              err.msg = "";
          }
        }

        return Promise.reject(err); // 
      }
    );

    // 
    options.url = toRestUrl(
      options.url,
      options.params ? options.params : options.data
    );

    instance(options)
      .then(res => {
        resolve(res);
      })
      .catch(error => {
        if (error && error.response) {
          let url = error.response.config.url.replace(
            Vue.prototype.$apiBaseURL,
            ""
          );
          url = url.replace(/^\/+|\/+$/gm, "");
          let searchResult = url.search(/^api\/.+?\/logout$/);
          if (error.response.status == 401) {
            if (
              error.response.data.detail == "TokenExpired" ||
              error.response.data.detail == "InvalidToken"
            ) {
              let token = sessionStorage.getItem("refreshToken");
              if (!token) {
                // 
                if (searchResult == -1) {
                  Message.info("token");
                }
                logout();
                return;
              }
              let refreshTokenRequestData = { refreshToken: token }; // token
              refreshToken(refreshTokenRequestData);
            } else if (
              error.response.data.detail == "TokenUserDisabledOrDeleted"
            ) {
              if (searchResult == -1) {
                Message.info("");
              }
              isRefreshing = false;
              logout();
            } else if (error.response.data.msg == "RefreshTokenExpired") {
              console.log("refersh token ");
              // 
              if (searchResult == -1) {
                Message.info("token");
              }
              isRefreshing = false;
              logout();
              return;
            }
            return;
          }

          if (
            error.response.status == 400 &&
            error.response.data.msg == "RefreshTokenNotExists"
          ) {
            console.log("refresh token ");

            // 
            if (searchResult == -1) {
              Message.info("token");
            }
            logout();
            return;
          }
        }

        reject(error);
      });
  });
}

//urlrestfulurl
function toRestUrl(url, params) {
  //   let paramArr = url.match(/(?<=\{).*?(?=\})/gi) // npm run build
  //   if (paramArr && paramArr.length) {
  //     if (Object.prototype.toString.call(params) === "[object FormData]") {
  //       // formData
  //       paramArr.forEach(el => {
  //         url = url.replace("{" + el + "}", params.get(el))
  //       })
  //     } else {
  //       paramArr.forEach(el => {
  //         url = url.replace("{" + el + "}", params[el])
  //       })
  //     }
  //   }
  let paramArr = url.match(/\{.*?\}/gi);
  if (paramArr && paramArr.length) {
    if (Object.prototype.toString.call(params) === "[object FormData]") {
      // formData
      paramArr.forEach(el => {
        let key = el.substring(1, el.length - 1); //  { } 
        url = url.replace(el, params.get(key));
      });
    } else {
      paramArr.forEach(el => {
        let key = el.substring(1, el.length - 1);
        url = url.replace(el, params[key]);
      });
    }
  }

  return url;
}

// 
function logout() {
  sessionStorage.clear();
  Cookies.remove("token");
  store.commit("setUserName", undefined);
  store.commit("setUserId", undefined);
  store.commit("setIsSuperUser", undefined); // 
  store.commit("setUserName", ""); // 
  store.commit("setMenuRouteLoadStatus", false); // false
  store.commit("updateMainTabs", []); // tab

  router.push("/login");
}

// token
function refreshToken(requestData) {
  if (!isRefreshing) {
    isRefreshing = true;
    request({
      url: "/api-token-auth/",
      method: "post",
      data: requestData
    })
      .then(res => {
        if (res.success) {
          Cookies.set("token", res.token); // tokenCookie
          Message.info("token");
        } else {
          Message.error(res.msg);
          logout();
        }
        isRefreshing = false;
      })
      .catch(error => {
        isRefreshing = false;
      });
  }
}

export default { request };



