<template>
    <el-form
        :model="loginForm"
        :rules="fieldRules"
        ref="loginForm"
        label-position="left"
        label-width="0px"
        class="demo-ruleForm login-container"
    >
        <h2 class="title"></h2>
        <el-form-item prop="account">
            <el-input
                type="text"
                v-model="loginForm.account"
                auto-complete="off"
                placeholder="\\"
                @keyup.enter.native="login"
            ></el-input>
        </el-form-item>
        <el-form-item prop="password">
            <el-input
                type="password"
                v-model="loginForm.password"
                auto-complete="off"
                placeholder=""
                @keyup.enter.native="login"
            ></el-input>
        </el-form-item>
        <!-- <el-form-item> -->
        <!-- <el-col :span="12">
                <el-form-item prop="captcha">
                    <el-input
                        type="test"
                        v-model="loginForm.captcha"
                        auto-complete="off"
                        placeholder=", "
                        style="width: 100%;"
                    ></el-input>
                </el-form-item>
        </el-col>-->
        <!-- <el-col class="line" :span="1">&nbsp;</el-col> -->
        <!-- <el-col :span="11">
                <el-form-item>
                    <img
                        style="width: 100%;"
                        class="pointer"
                        :src="loginForm.src"
                        @click="refreshCaptcha"
                    />
                </el-form-item>
        </el-col>-->
        <!-- </el-form-item> -->
        <!-- <el-checkbox v-model="checked" checked class="remember"></el-checkbox> -->
        <el-form-item style="width:100%;">
            <el-button type="primary" style="width:48%;" @click.native.prevent="reset"> </el-button>
            <el-button
                type="primary"
                style="width:48%;"
                @click.native.prevent="login"
                :loading="loading"
            > </el-button>
        </el-form-item>
    </el-form>
</template>

<script>
import { mapState } from "vuex";
import Cookies from "js-cookie";

export default {
    name: "Login",
    data() {
        return {
            loading: false,
            loginForm: {
                account: "admin",
                password: "admin",
                captcha: "",
                src: ""
            },
            fieldRules: {
                account: [
                    { required: true, message: "", trigger: "blur" }
                ],
                password: [
                    { required: true, message: "", trigger: "blur" }
                ]
                // ,
                // captcha: [
                //   { required: true, message: '', trigger: 'blur' }
                // ]
            }
            // checked: true
        };
    },
    methods: {
        login() {
            this.loading = true; // 
            let userInfo = {
                account: this.loginForm.account,
                password: this.loginForm.password,
                captcha: this.loginForm.captcha
            };
            this.$api.login
                .login(userInfo)
                .then(res => {
                    if (res.success) {
                        Cookies.set("token", res.data.token); // tokenCookie
                        sessionStorage.setItem(
                            "refreshToken",
                            res.data.refreshToken
                        ); // token

                        sessionStorage.setItem("name", res.data.name); // 
                        this.$store.commit(
                            "setUserGroups",
                            res.data.userGroups
                        ); // 
                        this.$store.commit("setUserId", res.data.userId); // id
                        this.$store.commit("setUserName", res.data.name); // 
                        this.$store.commit("setNavMenu", undefined); // 
                        this.$store.commit("setMenuRouteLoadStatus", false); // false
                        this.$store.commit("setUserPerms", res.data.perms); // 
                        this.$store.commit(
                            "setIsSuperUser",
                            res.data.isSuperuser
                        ); // 
        
                        if (JSON.stringify(this.$route.query) != "{}") {
                            this.$router.push(this.$route.query.redirect); // 
                        } else {// 
                            this.$router.push("/");
                        }
                        this.$message({
                            message: res.msg,
                            type: "success"
                        });
                    } else {
                        this.loading = false;
                        this.$message.error(res.msg);
                    }
                    this.loading = false;
                })
                .catch(res => {
                    this.$message.error(res.msg);
                    this.loading = false;
                });
        },
        refreshCaptcha: function() {
            this.loginForm.src =
                this.$globalConstant.baseUrl +
                "/captcha.jpg?t=" +
                new Date().getTime();
        },
        reset() {
            this.$refs.loginForm.resetFields();
        }
    },
    mounted() {
        this.refreshCaptcha();
    },
    computed: {
        ...mapState({
            themeColor: state => state.app.themeColor
        })
    }
};
</script>

<style lang="scss" scoped>
.login-container {
    -webkit-border-radius: 5px;
    border-radius: 5px;
    -moz-border-radius: 5px;
    background-clip: padding-box;
    margin: 100px auto;
    width: 400px;
    padding: 35px 35px 15px 35px;
    background: #fff;
    border: 1px solid #eaeaea;
    box-shadow: 0 0 25px #cac6c6;

    //  
    .title {
        margin: 0px auto 30px auto;
        text-align: center;
        color: #505458;
    }
    // .remember {
    //   margin: 0px 0px 35px 0px;
    // }
}
</style>


