import api from "@/common/network/api"

// 
export const getCurrentUserInfo = (resolve, reject) => {
  api.sysUser
    .getCurrentUserInfo()
    .then(res => {
      if (res.success) {
        resolve(res)
      } else {
        reject(res)
      }
    })
    .catch(res => {
      reject(res)
    })
}



