<template>
    <el-dialog
        title=""
        width="50%"
        :visible="dialogVisible"
        :show-close="showClose"
        :close-on-click-modal="closeOnClickModal"
        :close-on-press-escape="closeOnPressEscape"
        @close="onCloseDialog"
    >
        <div class="my-dialog-body">
            <!--  -->
            <div ref="actionBtn" style="text-align:right">
                <el-button type="primary" size="small" @click="addRelatedAccount"></el-button>
            </div>

            <!--  -->
            <el-table :data="tableData" stripe highlight-current-row>
                <!---->
                <el-table-column
                    prop="id"
                    label="ID"
                    header-align="center"
                    align="center"
                    width="60px"
                ></el-table-column>
                <el-table-column
                    prop="account"
                    label=""
                    header-align="center"
                    align="left"
                    min-width="200px"
                ></el-table-column>

                <el-table-column
                    prop="platform"
                    label=""
                    header-align="center"
                    align="center"
                    min-width="150px"
                ></el-table-column>

                <el-table-column label="" align="center" width="150px">
                    <template slot-scope="scope">
                        <el-button
                            size="mini"
                            @click="editRelatedAccount(scope.$index, scope.row)"
                            class="row-operation-btn"
                        ></el-button>

                        <el-button
                            size="mini"
                            type="danger"
                            @click="deleteRelatedAccount(scope.$index, scope.row)"
                        ></el-button>
                    </template>
                </el-table-column>
            </el-table>

            <!-- \ -->
            <bind-account-dialog
                v-if="bindAccountDialogVisible"
                :dialogVisible.sync="bindAccountDialogVisible"
                :dialogTitle="dialogTitle"
                :tableData="tableData"
                :row="row"
                :dialogFormData="accountData"
            ></bind-account-dialog>
        </div>
        <div class="dialog-footer" style="text-align:center">
            <el-button type="primary" @click="closeDialog"></el-button>
        </div>
    </el-dialog>
</template>

<script>
import BindAccountDialog from "./BindAccountDialog";
import { elDialogMixin } from "@/common/mixins/elDialogMixin";

export default {
    props: ["dialogVisible", "userId"],
    components: {
        BindAccountDialog,
    },
    mixins: [elDialogMixin],

    data() {
        return {
            tableData: [], // 
            accountData: null, // 
            row: [], // 
            bindAccountDialogVisible: false, // \true -  false - 
            dialogTitle: "", // 
        };
    },
    methods: {
        queryRelatedAccounts() {
            // 
            this.$api.sysUser
                .getRelatedAccounts({ userId: this.userId })
                .then((res) => {
                    if (res.success) {
                        this.tableData = res.data;
                    } else {
                        this.$message.error(res.msg);
                    }
                })
                .catch((res) => {
                    this.$message.error(res.msg || res.message);
                });
        },
        addRelatedAccount() {
            if (!this.bindAccountDialogVisible) {
                this.accountData = {
                    userId: this.userId,
                    account: "", // 
                    password: "", // 
                    platform: "",
                };

                this.bindAccountDialogVisible = true;
                this.dialogTitle = "";
            }
        },
        editRelatedAccount(index, row) {
            if (!this.bindAccountDialogVisible) {
                this.accountData = {
                    accountId: row.id,
                    userId: row.userId,
                    account: row.account, // 
                    password: row.password, // 
                    platform: row.platform,
                };
                this.row = row;
                this.bindAccountDialogVisible = true;
                this.dialogTitle = "";
            }
        },

        // 
        deleteRelatedAccount(index, row) {
            this.$confirm("?", "", {
                confirmButtonText: "",
                cancelButtonText: "",
                type: "warning",
                cancelButtonClass: "btn-custom-cancel",
            })
                .then(() => {
                    // 
                    this.$api.sysUser
                        .deleteRelatedAccount({
                            userId: row.userId,
                            accountId: row.id,
                        })
                        .then((res) => {
                            if (res.success) {
                                this.$message({
                                    message: res.msg,
                                    type: "success",
                                });

                                this.tableData.splice(index, 1); // 
                            } else {
                                this.$message.error(res.msg);
                            }
                        })
                        .catch((res) => {
                            this.$message.error(res.msg || res.message);
                        });
                })
                .catch(() => {});
        },
    },
    created() {
        this.queryRelatedAccounts();
    },
};
</script>

<style lang="scss" scoped>
.dialog-footer {
    margin-top: 5px;
    text-align: right;
    position: relative;
}
</style>



