export default {
  state: {
    testcasesCopied: [], // 
    testcasesCut: [] // 
  },
  mutations: {
    setTestcasesCopied(state, testcasesCopied) {
      state.testcasesCopied = testcasesCopied
    },
    setTestcasesCut(state, testcasesCut) {
      state.testcasesCut = testcasesCut
    }
  }
}



