export default {
  state: {
    // groupID: "", // sprintID
    groupType: "", //API
    groupID: -1, // APIID
    groupName: "" // API
    // productID: -1, // ID
    // parentID: -2, // ID
    // suitePath: "" // 
  },
  mutations: {
    // updateAPIProjectGroupID(state, groupID) {
    //   state.groupID = groupID;
    // },
    updateCurrentGroup(state, groupInfo) {
      state.groupType = groupInfo.groupType;
      state.groupID = groupInfo.groupID;
      state.groupName = groupInfo.groupName;
    }
  }
};



